import React, { useState, useEffect } from 'react'
import CitizenApplyApiList from './CitizenApplyApiList'
import axios from 'axios'
import CitizenMessageScreen from './CitizenMessageScreen'
import Dashboard from '../Pages/CitizenDashboardPage/Dashboard'
import { Link } from 'react-router-dom'
import BackComponent from './BackComponent'


function AllAppliedApplicationList() {

    const { api_getAllApplicationList } = CitizenApplyApiList()

    const [allApplicationList, setallApplicationList] = useState()
    const [messageScreen, setmessageScreen] = useState(false)
    const [applicationId, setapplicationId] = useState()
    const [workflowId, setworkflowId] = useState()

    const popUpMessageBox = (e) => {

        console.log("------e.target.values/id", e.target.value, e.target.id)
        setapplicationId(e.target.value)
        setworkflowId(e.target.id)

        messageScreen ? setmessageScreen(false) : setmessageScreen(true)
    }


    let token = window.localStorage.getItem('token')
    // let token = "1719|W5IgGaCve03ZdVNaacsaNW55Js2WnwqAT3bnIMoc"
    const header = {
        headers:
        {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
        }
    }

    //{////********ApplicationList*******//////}
    const fetchApplicationList = () => {
        axios.get(api_getAllApplicationList,
            // {
            //     module: module.module
            // },
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            }
        )
            .then(function (response) {
                console.log("all apllication list----- ", response.data.data);
                setallApplicationList(response.data.data)

            })
            .catch(function (error) {
                console.log(error);
            })

    }

    useEffect(() => {
        fetchApplicationList();
    }, [])

    console.log("all apllication list 2----- ", allApplicationList);
    return (
        <>

            <div className='-mt-8 p-2 '>
                <Link to='/dashboardEntry'>
                    <BackComponent />
                </Link>
            </div>
            <div>

                <div className='bg-gray-50 rounded-lg mt-4'>
                    <Dashboard />

                    <div className=' md:p-2 flex justify-center items-center    mb-2'>

                        <div className={`grid grid-cols-12 bg-gray-50 shadow-lg w-full md:w-full p-3 md:p-3 gap-3 rounded-lg`}>
                            <div className="col-span-12">
                                <div className='flex flex-row'>
                                    <div className='flex flex-row  text-sky-500 hover:bg-sky-50 p-1'>
                                        <img src='https://cdn-icons-png.flaticon.com/512/3187/3187927.png' className='h-6  ml-3' />
                                        <h1 className='font-mono text-lg '>All Applied Applications</h1>
                                    </div>
                                    <div className='flex flex-row bg-slate-200 text-sky-500 hover:bg-sky-50 p-2'>
                                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-5 h-5">
                                            <path stroke-linecap="round" stroke-linejoin="round" d="M11.25 11.25l.041-.02a.75.75 0 011.063.852l-.708 2.836a.75.75 0 001.063.853l.041-.021M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9-3.75h.008v.008H12V8.25z" />
                                        </svg>

                                        <h1 className='font italic text-xs ml-2 '>you can suggest or request  related to your application by clicking on message icon</h1>
                                    </div>
                                </div>
                            </div>
                            <div className='flex flex-row space-x-3 p-2'>
                                <div className='flex flex-row'>
                                    <h1 className='w-6 h-6 bg-teal-400 rounded-full text-xs text-center px-1 py-1 shadow-lg shadow-sky-300 text-white'>{allApplicationList?.Property?.totalApplications}</h1>
                                    <button className='text-sm ml-1'>Property </button>
                                </div>
                                <div className='flex flex-row'>
                                    <h1 className='w-6 h-6 bg-teal-400 rounded-full text-xs text-center px-1 py-1 shadow-lg shadow-sky-300 text-white'>{allApplicationList?.Trade?.totalApplications}</h1>
                                    <button className='text-sm ml-1'>Trade </button>
                                </div>
                                <div className='flex flex-row'>
                                    <h1 className='w-6 h-6 bg-teal-400 rounded-full text-xs text-center px-1 py-1 shadow-lg shadow-sky-300 text-white'>{allApplicationList?.Water?.totalApplications}</h1>
                                    <button className='text-sm ml-1'>Water </button>
                                </div>

                            </div>

                            {/* {****** Property Application List    *********} */}
                            <div className='col-span-12  mt-4 '>
                                {/* {******  TITTLE *********} */}

                                <div className="col-span-12 flex flex-row w-full  text-lg   ">
                                    <h1 className={`font-semibold text-gray-500 text-sm ml-2 text-center `}>PROPERTY APPLIED APPLICATION</h1>
                                </div>

                                {/* {****** Property Application List    *********} */}

                                <div className="col-span-12  w-full p-2 shadow-md text-center text-xs">

                                    <div className='grid grid-cols-6 md:grid-cols-6 lg:grid-cols-6 gap-2   '>
                                        <div className=' '>
                                            <h1 className=''>ACTION</h1>
                                        </div>
                                        <div className=' '>
                                            <h1 className=''>STATUS</h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>SAF NO.</h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>HOLDING NO.</h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>ASSESSMENT TYPE </h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>APPLY DATE </h1>
                                        </div>
                                    </div>

                                </div>

                                <div className='col-span-12 w-full bg-white h-44 text-xs text-center overflow-y-scroll '>
                                    {
                                        allApplicationList?.Property?.applications?.map((items) => (


                                            <div className='grid grid-cols-6 md:grid-cols-6 lg:grid-cols-6 gap-2 py-2 shadow-md shadow-gray-300 mb-4  bg-white '>
                                                <div className='flex flex-row space-x-2 mx-auto'>
                                                    <div>
                                                        <button className='text-center   text-xs'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-5 h-5 text-[#0369a1]">
                                                            <path d="M12 15a3 3 0 100-6 3 3 0 000 6z" />
                                                            <path fill-rule="evenodd" d="M1.323 11.447C2.811 6.976 7.028 3.75 12.001 3.75c4.97 0 9.185 3.223 10.675 7.69.12.362.12.752 0 1.113-1.487 4.471-5.705 7.697-10.677 7.697-4.97 0-9.186-3.223-10.675-7.69a1.762 1.762 0 010-1.113zM17.25 12a5.25 5.25 0 11-10.5 0 5.25 5.25 0 0110.5 0z" clip-rule="evenodd" />
                                                        </svg>
                                                        </button>
                                                    </div>
                                                    <div>
                                                        <button type='button' value={items?.application_id} id={items?.workflow_id} className='text-center  text-xs' onClick={popUpMessageBox}> <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-5 h-5 text-[#0369a1] ">
                                                            <path d="M1.5 8.67v8.58a3 3 0 003 3h15a3 3 0 003-3V8.67l-8.928 5.493a3 3 0 01-3.144 0L1.5 8.67z" />
                                                            <path d="M22.5 6.908V6.75a3 3 0 00-3-3h-15a3 3 0 00-3 3v.158l9.714 5.978a1.5 1.5 0 001.572 0L22.5 6.908z" />
                                                        </svg>
                                                        </button>
                                                    </div>
                                                </div>

                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.saf_pending_status == 0 ? "PENDING " : "APPROVED"}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.saf_no}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.holding_no}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.assessment_type}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.created_at}    </span>
                                                </div>
                                            </div>

                                        ))
                                    }


                                </div>

                            </div>


                            {/* {****** Trade Application List    *********} */}
                            <div className='col-span-12 mt-4'>
                                {/* {******  TITTLE *********} */}
                                <div className="col-span-12 flex flex-row w-full  text-lg   ">
                                    <h1 className={`font-semibold text-gray-500 text-sm ml-2 text-center `}>TRADE APPLIED APPLICATION</h1>
                                </div>

                                {/* {******  Application List    *********} */}

                                <div className="col-span-12  w-full bg-gray-50 p-2 shadow-md text-center text-xs">

                                    <div className='grid grid-cols-5 md:grid-cols-5 lg:grid-cols-5 gap-2'>
                                        <div className=' '>
                                            <h1 className=''>ACTION</h1>
                                        </div>
                                        <div className=' '>
                                            <h1 className=''>STATUS</h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>APPLICATION NO.</h1>
                                        </div>
                                        <div className=''>
                                            <h1 className=''>HOLDING NO.</h1>
                                        </div>

                                        <div className=''>
                                            <h1 className=''>APPLY DATE </h1>
                                        </div>
                                    </div>

                                </div>

                                <div className='col-span-12 w-full h-44 text-xs text-center overflow-y-scroll overflow-x-scroll'>
                                    {
                                        allApplicationList?.Trade?.applications?.map((items) => (


                                            <div className='grid grid-cols-5 md:grid-cols-5 lg:grid-cols-5 gap-2 py-2 shadow-md shadow-gray-300 mb-4  bg-white '>
                                                <div className='flex flex-row space-x-2 mx-auto'>
                                                    <div>
                                                        <button className='text-center   text-xs'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-5 h-5 text-[#0369a1]">
                                                            <path d="M12 15a3 3 0 100-6 3 3 0 000 6z" />
                                                            <path fill-rule="evenodd" d="M1.323 11.447C2.811 6.976 7.028 3.75 12.001 3.75c4.97 0 9.185 3.223 10.675 7.69.12.362.12.752 0 1.113-1.487 4.471-5.705 7.697-10.677 7.697-4.97 0-9.186-3.223-10.675-7.69a1.762 1.762 0 010-1.113zM17.25 12a5.25 5.25 0 11-10.5 0 5.25 5.25 0 0110.5 0z" clip-rule="evenodd" />
                                                        </svg>
                                                        </button>
                                                    </div>
                                                    <div>
                                                        <button type='button' value={items?.application_id} id={items?.workflow_id} className='text-center  text-xs' onClick={popUpMessageBox}> <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-5 h-5 text-[#0369a1]">
                                                            <path d="M1.5 8.67v8.58a3 3 0 003 3h15a3 3 0 003-3V8.67l-8.928 5.493a3 3 0 01-3.144 0L1.5 8.67z" />
                                                            <path d="M22.5 6.908V6.75a3 3 0 00-3-3h-15a3 3 0 00-3 3v.158l9.714 5.978a1.5 1.5 0 001.572 0L22.5 6.908z" />
                                                        </svg>
                                                        </button>
                                                    </div>
                                                </div>

                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.saf_pending_status == 0 ? "PENDING " : "APPROVED"}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.application_no}</span>
                                                </div>
                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.holding_no}</span>
                                                </div>

                                                <div className=''>
                                                    <span className='text-xs text-gray-600 font-semibold'>{items?.created_at}    </span>
                                                </div>
                                            </div>

                                        ))
                                    }
                                </div>
                            </div>

                            {/******************** Message******************************/}
                            <div>
                                {messageScreen ? <CitizenMessageScreen fun={setmessageScreen} applicationIdfun={applicationId} workflowIdfun={workflowId} /> : ''}
                            </div>

                        </div>
                    </div>

                </div>

            </div>

        </>
    )
}

export default AllAppliedApplicationList